import React, { useRef, useState } from "react";
import { gsap } from "gsap";

const OurTeam = () => {
  const [currentCard, setCurrentCard] = useState(0); // Track the current card index
  const cardContainerRef = useRef(null);
  const totalCards = 2; // Number of cards (change accordingly if more are added)

  const swipeNextCard = () => {
    if (currentCard < totalCards - 1) {
      // Move the cards to the left to reveal the next one
      gsap.to(cardContainerRef.current, {
        x: `-=${window.innerWidth}`, // Slide the container left by the window width (full swipe)
        duration: 0.5,
        ease: "power2.out",
        onComplete: () => {
          setCurrentCard(currentCard + 1); // Update the card index after swipe
          gsap.set(cardContainerRef.current, { x: 0 }); // Reset container position
        },
      });
    }
  };

  const swipePrevCard = () => {
    if (currentCard > 0) {
      // Move the cards to the right to reveal the previous one
      gsap.to(cardContainerRef.current, {
        x: `+=${window.innerWidth}`, // Slide the container right by the window width
        duration: 0.5,
        ease: "power2.out",
        onComplete: () => {
          setCurrentCard(currentCard - 1); // Update the card index after swipe
          gsap.set(cardContainerRef.current, { x: 0 }); // Reset container position
        },
      });
    }
  };

  return (
    <div className="flex items-center justify-center h-screen">
      <div className="flex flex-col gap-12 items-center w-11/12 md:w-10/12">
        <div className="w-full flex flex-col gap-6 md:gap-0 md:flex-row justify-between">
          <div className="w-full md:w-1/6">
            <p className="text-muted-foreground">/ Company /</p>
          </div>
          <div className="w-full md:w-5/6 flex flex-col gap-4 md:gap-0 md:flex-row justify-between">
            <div className="w-full md:w-1/3">
              <h3 className="text-3xl font-medium">Meet our passionate team</h3>
            </div>
            <div className="cards w-full md:w-3/5 relative flex justify-center border h-fit overflow-hidden">
              <div
                ref={cardContainerRef}
                className="flex transition-transform ease-in-out"
              >
                <div className="card w-10/12 md:w-3/5 h-80 md:h-96 bg-blue-500 shadow-lg z-10 rounded-xl overflow-hidden">
                  <img
                    src="/images/member1.jpeg"
                    alt="Team Member"
                    className="w-full h-full object-cover"
                  />
                  <p className="text-center text-white mt-2">Member 1</p>
                </div>
                <div className="card w-10/12 md:w-3/5 h-80 md:h-96 bg-blue-500 shadow-lg z-10 rounded-xl overflow-hidden">
                  <img
                    src="/images/member1.jpeg"
                    alt="Team Member"
                    className="w-full h-full object-cover"
                  />
                  <p className="text-center text-white mt-2">Member 2</p>
                </div>
                {/* Add more cards here */}
              </div>
            </div>
            <div className="flex justify-between w-full mt-4">
              <button
                onClick={swipePrevCard}
                className="bg-gray-500 text-white py-2 px-4 rounded"
                disabled={currentCard === 0}
              >
                Prev
              </button>
              <button
                onClick={swipeNextCard}
                className="bg-blue-500 text-white py-2 px-4 rounded"
                disabled={currentCard === totalCards - 1}
              >
                Next
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default OurTeam;
