import React from "react";
import Accordion from "./Accordian";
import WhyChooseUs from "./WhyChooseUs";

const accordionItems = [
  {
    title: "Digital Marketing",
    content:
      "We utilize advanced AI technology to optimize your marketing strategies and ensure sustained growth.",
  },
  {
    title: "Creative Branding & Design",
    content:
      "We offer expert graphic design, brand identity development, and videography services to help your brand stand out in a competitive market.",
  },
  {
    title: "Comprehensive Marketing Solutions",
    content:
      "A one-stop agency for all your digital marketing needs, including SEO, social media management, content creation, and digital advertising.",
  },
  {
    title: "Scalable & Remote-Friendly",
    content:
      "Flexible, scalable digital marketing strategies that grow with your business, whether you're a startup, small business, or enterprise.",
  },
];

const OurServices = () => {
  return (
    <div className="flex items-center justify-center">
      <div className="flex flex-col gap-12 items-center w-11/12 md:w-10/12">
        <WhyChooseUs />

        <div className="w-full">
          {accordionItems.map((item, index) => (
            <Accordion key={index} title={item.title} index={index}>
              <p className="text-sm md:text-base leading-6 text-muted-foreground">
                {item.content}
              </p>
            </Accordion>
          ))}
        </div>
      </div>
    </div>
  );
};

export default OurServices;
